





















import { Component, Vue, Watch } from 'vue-property-decorator';
import { MusicLoading } from './components';

@Component({
  components: {
    MusicLoading,
  },
})
export default class App extends Vue {
  public $refs !: {
    audio: HTMLAudioElement,
  };
  private audio !: HTMLAudioElement;

  private routerInclude: string = 'home';

  private routeArr: string [] = [];

  private routeAni: string = '';

  @Watch('currentMusic')
  private watchCurrentMusic(currentMusic: any) {
    this.audio.src = currentMusic.file128;
    this.audio.addEventListener('canplay', () => {
      this.audio.play();
    });
  }

  @Watch('isPlay')
  private watchIsPlay(isPlay: boolean) {
    if (isPlay) {
      this.audio.play();
    } else {
      this.audio.pause();
    }
  }

  @Watch('seeked')
  private watchSeeked(seeked: number) {
    this.audio.currentTime = seeked;
  }

  get currentMusic() {
    return this.$store.state.player.musicList[this.$store.state.player.currentMusicIndex];
  }

  get isPlay() {
    return this.$store.state.player.isPlay;
  }

  get seeked() {
    return this.$store.state.player.seeked;
  }

  get currentTime() {
    return this.$store.state.player.currentTime;
  }

  get duration() {
    return this.$store.state.player.duration;
  }

  private mounted() {
    this.audio = this.$refs.audio;
    this.audio.addEventListener('ended', () => {
      this.$store.commit('player/pause');
      this.audio.pause();
    });
    this.audio.addEventListener('timeupdate', (e: any) => {
      this.$store.commit('player/setCurrentTime', e.target.currentTime);
    });
    this.audio.addEventListener('durationchange', (e: any) => {
      const time = e.target.duration;
      if (!isNaN(time)) {
        this.$store.commit('player/setDuration', time);
      }
    });

    this.$router.beforeEach((to, from, next) => {
      const routeIndex = this.routeArr.indexOf(`${to.name}`);
      if (routeIndex === -1) {
        this.routeAni = 'fade';
        this.routeArr.push(`${from.name}`);
        this.routeArr.push(`${to.name}`);
      } else {
        this.routeAni = 'defa';
        this.routeArr.splice(routeIndex + 1);
      }

      if (/(detail|player)/.test(`${from.name}`) && /(player|detail)/.test(`${to.name}`) ) {
        this.routerInclude = 'home,detail';
      } else {
        this.routerInclude = 'home';
      }
      next();
    });
  }

  private toPlayer() {
    this.$router.push(`/player/${this.currentMusic.id}`);
  }
}
