
















import { Component, Vue, Prop, Watch } from 'vue-property-decorator';

@Component
export default class Slide extends Vue {

  @Prop()
  private change !: () => {};

  @Prop({})
  private duration !: number;

  @Prop({})
  private currentTime !: number;

  public $refs !: {
    slider: Element,
  };

  private left: number = 40;
  private width: number = 0;
  private playProgress: number = 0;
  private dragProgress: number = 0;
  private flag: boolean = false;

  @Watch('progress')
  private watchProgress(progress: number) {
    if (!this.flag) {
      this.playProgress = progress;
    }
  }

  get progress() {
    return this.currentTime / this.duration;
  }

  private data() {
    return {
      test: null,
    };
  }

  private mounted() {
    const clientRect = this.$refs.slider.getBoundingClientRect();
    this.width = clientRect.width;
  }

  private touchstart(e: TouchEvent) {
    const width = e.changedTouches[0].clientX - this.left;
    this.dragProgress = width <= 0 ? 0 : width;
    this.playProgress = this.dragProgress / this.width;
    this.flag = true;
  }

  private touchmove(e: TouchEvent) {
    const width = e.changedTouches[0].clientX - this.left;
    this.dragProgress = Math.min(width <= 0 ? 0 : width, this.width);
    this.playProgress = this.dragProgress / this.width;
  }
  private touchend(e: TouchEvent) {
    this.$emit('change', this.dragProgress / this.width * this.duration);
    this.playProgress = this.dragProgress / this.width;
    this.flag = false;
  }
}
