import { findSongIndex } from '../helps';
const player = {
  namespaced: true,
  state: {
    isPlay: false,
    currentMusicIndex: 0,
    musicList: [],
    currentTime: 0,
    duration: 0,
    seeked: 0,
  },
  mutations: {
    add(state: any, music: any) {
      const index = findSongIndex(state.musicList, music.id);
      if (index !== -1) {
        state.currentMusicIndex = index;
        (this as any).commit('player/play');
        return;
      }
      state.musicList.push(music);
      if (state.musicList.length !== 0) {
        (this as any).commit('player/next');
      }
      (this as any).commit('player/play');
    },
    play(state: any) {
      state.isPlay = true;
    },
    pause(state: any) {
      state.isPlay = false;
    },
    next(state: any) {
      let index = state.currentMusicIndex + 1;
      if (index >= state.musicList.length) {
        index =  0;
      }
      state.currentMusicIndex = index;
    },
    prev(state: any) {
      let index = state.currentMusicIndex - 1;
      if (index < 0) {
        index = state.musicList.length - 1;
      }
      state.currentMusicIndex = index;
    },
    setCurrentTime(state: any, time: number) {
      state.currentTime = time;
    },
    setDuration(state: any, time: number) {
      state.duration = time;
    },
    setSeeked(state: any, time: number) {
      state.seeked = time;
    },
  },
};

export default player;
