<template>
 <div class="music-loading">
   <div class="wrap">
    <div class="box1">
      <div class="item1" :style="{transform: `rotate(${(progress>0.5?progress-0.5:0)*360}deg) translateZ(0)`}"></div>
    </div>
    <div class="box2">
      <div class="item2" :style="{transform: `rotate(${Math.min(0.5, progress)*360}deg) translateZ(0)`}"></div>
    </div>
    <div class="img-wrap">
      <img :class="{'animation-rotate':playing}" :src="imgUrl" />
    </div>
  </div>
 </div>
</template>
<script>
export default {
 props: {
   totalTime: {
     type: Number,
     default: 0,
   },
   currentTime: {
     type: Number,
     default: 0,
   },
   imgUrl: {
     type: String,
     default: '',
   },
   playing: {
     type: Boolean,
     default: false,
   },
 },
 computed: {
   progress() {
     return this.currentTime / this.totalTime;
   },
 },
};
</script>
<style lang="scss">
@keyframes rotate {
  form{
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
.animation-rotate {
  animation: rotate 10s infinite linear;
}
.music-loading {
  position: fixed;
  bottom: 40px;
  right: 0;
  z-index: 1;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 5px;
  padding-right: 20px;
  border-radius: 20px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  display: flex;
  align-items: center;
  background: #fff;
  box-shadow: 0px 0px 5px rgba(0,0,0,0.5);
  .wrap {
    width: 25px;
    height: 25px;
    position: relative;
    margin-left: 5px;
    .img-wrap {
      width: 22.5px;
      height: 22.5px;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      overflow: hidden;
      img {
        width: 100%;
      }
    }
  }
  .box1,.box2 {
    width: 12.5px;
    height: 25px;
    position: absolute;
    overflow: hidden;
  }
  .box1 {
    left: 0;
  }
  .box2 {
    right: 0;
  }
  .item1,.item2 {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: 2.5px solid red;
    top: 0;
    position: absolute;
  }
  .item1 {
    left: 0;
    clip: rect(auto auto auto 12.5px);
  }
  .item2 {
    left: -100%;
    clip: rect(auto 12.5px auto auto);
  }
}

</style>

