






















import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Huntings extends Vue {

  private startY: number = 0;
  private topFlag: boolean = false;
  private bottomFlag: boolean = false;

  public $refs !: {
    dom: HTMLElement,
  };

  private touchstart(e: TouchEvent) {
    if (this.$refs.dom.scrollTop === 0 && !this.topFlag) {
      this.topFlag = true;
      this.startY = e.changedTouches[0].clientY;
      this.$refs.dom.dataset.y = '0';
      this.$refs.dom.classList.remove('transtion');
    }
  }

  private touchmove(e: TouchEvent) {
    if (Number(this.$refs.dom.dataset.y) <= 100 && this.topFlag && e.changedTouches[0].clientY > this.startY) {
     let y = e.changedTouches[0].clientY - this.startY;
     y =  Math.min(y, 100);
     this.$refs.dom.dataset.y = `${y}`;
     this.$refs.dom.style.transform = 'translate3d(0,' + y + 'px,0)';
    }
  }

  private touchend(e: TouchEvent) {
    if (this.topFlag && e.changedTouches[0].clientY > this.startY) {
      this.$emit('topMethod', () => {
        this.$refs.dom.classList.add('transtion');
        this.$refs.dom.dataset.y = '0';
        this.$refs.dom.style.transform = 'translate3d(0,' + 0 + 'px,0)';
        this.topFlag = false;
      });
    }
  }

  private scroll() {
    if (!this.bottomFlag && this.$refs.dom.scrollTop + 40 + this.$refs.dom.clientHeight >= this.$refs.dom.scrollHeight) {
      this.bottomFlag = true;
      this.$emit('bottomMethod', () => {
        this.bottomFlag = false;
      });
    }
  }
}
