

























import { Component, Vue, Prop } from 'vue-property-decorator';
@Component({})
export default class SongList extends Vue {
  @Prop()
  private data !: object [];

  @Prop()
  private selectSongId !: number;

  @Prop()
  private selectSong !: () => {};

  private click(item: any) {
    this.$emit('selectSong', item);
  }
}
