import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import(/* webpackChunkName: "home" */ './views/Home.vue'),
      redirect: '/music-article',
      children: [
        {
          path: '/music-article',
          name: 'music-article',
          component: () => import(/* webpackChunkName: "MusicArticle" */ './views/MusicArticle.vue'),
        },
        {
          path: '/huntings',
          name: 'huntings',
          component: () => import(/* webpackChunkName: "Huntings" */ './views/Huntings.vue'),
        },
        {
          path: '/melody',
          name: 'melody',
          component: () => import(/* webpackChunkName: "Melody" */ './views/Melody.vue'),
        },
        {
          path: '/indie-artist',
          name: 'indie-artist',
          component: () => import(/* webpackChunkName: "IndieArtist" */ './views/IndieArtist.vue'),
        },
        {
          path: '/music-radio',
          name: 'music-radio',
          component: () => import(/* webpackChunkName: "MusicRadio" */ './views/MusicRadio.vue'),
        },
      ],
    },
    {
      path: '/about',
      name: 'about',
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue'),
    },
    {
      path: '/music-article/detail/:id',
      name: 'music-article-detail',
      component: () => import(/* webpackChunkName: "MusicArticleDetail" */ './views/MusicArticleDetail.vue'),
    },
    {
      path: '/huntings/detail/:id',
      name: 'huntings-detail',
      component: () => import(/* webpackChunkName: "HuntingsDetail" */ './views/HuntingsDetail.vue'),
    },
    {
      path: '/melody/detail/:id',
      name: 'melody-detail',
      component: () => import(/* webpackChunkName: "MelodyDetail" */ './views/MelodyDetail.vue'),
    },
    {
      path: '/indie-artist/detail/:id',
      name: 'indie-artist-detail',
      component: () => import(/* webpackChunkName: "IndieArtistDetail" */ './views/IndieArtistDetail.vue'),
    },
    {
      path: '/comment/:id',
      name: 'comment',
      component: () => import(/* webpackChunkName: "Comment" */ './views/Comment.vue'),
    },
    {
      path: '/player/:id',
      name: 'player',
      component: () => import(/* webpackChunkName: "Player" */ './views/Player.vue'),
    },
  ],
});
